@import '../../variables.scss';

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1350px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  &__logo {
    background-image: url(../../../public/img/logo.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 79px;
    height: 62px;
  }
}
